<template>
  <div class="outline-wrap">
    <div class="head">{{cataName}}</div>
    <el-form class="create-form" label-width="90px">
      <el-form-item label="教学目的：">
        <p v-html="teachTarget"></p>
      </el-form-item>
      <el-form-item label="教学重点：">
        <p v-html="teachPoint"></p>
      </el-form-item>
      <el-form-item label="教学难点：">
        <p v-html="teachRub"></p>
      </el-form-item>
      <el-form-item label="教学要求：">
        <p v-html="teachRules"></p>
      </el-form-item>
      <el-form-item label="描述：">
        <p v-html="teachDesc"></p>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import http from "../../../../utils/http";
import api from "../../../../utils/api";
export default {
  data() {
    return {
      tcid: "",
      cataName: "",
      teachTarget: "",
      teachPoint: "",
      teachRub: "",
      teachRules: "",
      teachDesc: ""
    };
  },
  mounted() {
    this.tcid = this.$route.query.tcid;
    this.getData();
  },
  methods: {
    getData: async function() {
      const res = await http.get(api.getTeachingContentData, {
        id: this.tcid,
        Token: http.getToken()
      });
      if (res.Result) {
        if (res.Data == null) return;

        this.cataName = res.Data.TCTitle;
        this.teachTarget = res.Data.TCAims;
        this.teachPoint = res.Data.TCKeyPoint;
        this.teachRub = res.Data.TCDifficulty;
        this.teachRules = res.Data.TCDemand;
        this.teachDesc = res.Data.TCDesc;
      }
    }
  },
  watch: {
    $route: {
      handler: function(to, from) {
        this.tcid = to.query.tcid;
        this.getData();
      },
      deep: true
    }
  }
};
</script>
<style lang="less" scoped>
</style>


